<template>
  <div ref="mainContainer" class="project-container" v-loading="loading">
    <div v-for="item in data" :key="item.id" class="project-item">
      <img :src="item.thumb" v-layz="item.thumb" alt="" class="thumb" />
      <div class="info">
        <a
          :target="item.url ? '_blank' : '_self'"
          :href="item.url ? item.url : `javascript:alert('该项目无法访问')`"
        >
          <h2>{{ item.name }}</h2>
        </a>
        <a v-if="item.github" :target="'_blank'" :href="item.github">github</a>
        <p v-for="(desc, i) in item.description" :key="i">{{ desc }}</p>

        <p>{{ item.description }}</p>
      </div>
    </div>
    <!-- 无数据显示 -->
    <Empty v-if="data.length === 0 && !loading " />
  </div>
  
    

</template>

<script>
import { mapState } from "vuex";
import Empty from "@/components/Empty.vue"

//由于需要懒加载 导入事件总线
import mainScroll from "@/mixins/mainScroll";
export default {
  mixins: [mainScroll("mainContainer")],
  components:{
    Empty,
  },
  computed: mapState("project", ["loading", "data"]),
  created() {
    this.$store.dispatch("project/fetchProject");
  },
};
</script>

<style scoped lang="less">
.project-container {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
  position: relative;
  overflow-y: auto;
  scroll-behavior: smooth;
}
.project-item {
  transition: 0.5s;
  padding: 20px;
  display: flex;
  margin-bottom: 20px;
  &:hover {
    box-shadow: -1px 1px 5px #000;
    transform: scale(1.01) translate(3px, -3px);
    color: inherit;
  }
  .thumb {
    width: 250px;
    min-height: 150px;
    flex: 0 0 auto;
    object-fit: cover;
    border-radius: 5px;
    margin-right: 15px;
  }
  .info {
    line-height: 1.7;
    flex: 1 1 auto;
    h2 {
      margin: 0;
    }
  }
}
</style>